import React from 'react';
import Button from 'react-bootstrap/Button';
import $ from 'jquery';
import '../../css/App.css';
import { useEffect } from 'react';
import colorspectrum from '../../images/raw/large/Spectrum.png';
import exampleImage from '../../images/raw/large/Elephant2.jpg';
import { useNavigate } from 'react-router-dom';
import Navbar from '../../components/Navbar';

const Home = () => {
	useEffect(() => {
		createPixelMask();
	}, [])

	let navigate = useNavigate();

	function createPixelMask(){
		var table = document.getElementById("pixel_Table");
		var row;
		var cell;
    	for(var i=0;i<7;i++){
			row = table.insertRow(i);
      		for(var j=0;j<7;j++){
			    cell = row.insertCell(j);
			    cell.addEventListener('click', IncreaseOpacity);
				cell.addEventListener('contextmenu', DecreaseOpacity)
        	}
    	}

    	document.getElementById("pixel_Table").style.backgroundImage = 'url(' + exampleImage + ')';
    }
		
	function DecreaseOpacity(event) {
		event.preventDefault()
		var opacity_level = parseFloat($(event.target).css("opacity"));
		if (opacity_level > "0.0") {
			opacity_level = opacity_level - 0.30;
			$(event.target).css("opacity", opacity_level);
		}
	}
		
	function IncreaseOpacity(event) {
		var opacity_level = parseFloat($(event.target).css("opacity"));
		if (opacity_level < "0.9") {
			opacity_level = opacity_level + 0.30;
			$(event.target).css("opacity", opacity_level);
		}	
	}

    return (
		<div id="Home">
			<div className="main-container">
					<div className="header">
						<Navbar/>
					</div>
				<div className="container">
					<div className="content">	           
						<div className="row">
							<div style={{float: "left", width: "50%", textAlign: "left"}}>
								<p>Enkten bestr utav tv delar. I den frsta delen kommer du f se <b>5 bilder</b> p olika djur. Som respondent vill vi att du med hjlp utav ett rutnt markerar vilka delar av en bild som du anser r viktig fr att du ska kunna identifiera djuret. I den andra delen ber vi dig att <b>rangordna</b> olika frklaringar, samt svara p <b>X frgor</b>.</p>
								<br></br>
								<p>Fr den frsta delen av enkten s vill vi frtydliga att det inte r ditt uppdrag att identifiera djuren, utan att indikera vilka delar av en bild du behver, och till vilken grad, fr att utfra identifikationen. Detta gr du genom att klicka p rutorna i bilden. Det finns <b>4 niver</b> av markering, dr ingen markering r niv 1</p>
								<br></br>
								<p>I exempelbilden till hger kan du klicka runt och testa hur den frsta delen kommer att fungera. Det finns inget minimalt eller maximalt antal av rutorna som mste markeras, men vi ser grna att du markerar <b>mer n 1 ruta</b> och frsker titta p varje ruta individuellt fr att avgra hur viktig den r. Fr elefanten skulle man till exempel kunna markera rutorna som inkluderar snabeln och ge hgre niver av markering dr det r tydligare att det r en snabel man tittar p.</p>
								<br></br>
								<p>
									Har du frgor innan du pbrjar enkten kan du kontakta oss p
									<br>
									</br>
									xaiundersokning@gmail.com
								</p>
								<br></br>	
							</div>
							<div style={{float: "left", width: "50%", textAlign: "center"}}>
								<h5>Exempel</h5>
								<table id="pixel_Table"></table>
								<p>
									<small>
										<i>
											Vnsterklick kar nivn fr en ruta med ett steg, medan 
											<br>
											</br>
											hgerklick minskar ett steg. Hgre niv indikerar att
											<br>
											</br>
											rutan r viktigare fr att kunna identifiera djuret.
										</i>
									</small>
								</p>
								<figure>
								<img src={colorspectrum} alt="Spektrum" style={{border: "1px solid black"}}/>
									<figcaption>
										<h5>
											Gradskala
										</h5>
									</figcaption>
								</figure>
								<p>
								Tryck p Starta enkten nr du r redo.
								</p>
								<Button variant="outline-secondary" onClick={() => {
									navigate('/survey');
								}}
								>Starta enkten</Button> 	
							</div>
						</div>
					</div>	
				</div> 
			</div>
		</div>
    )
}

export default Home;