import Button from 'react-bootstrap/Button';
import GetUserId from '../utils/GetUserId';
import { useEffect } from "react";

const SurveyPart3 = () => {
    const userId = GetUserId();
    var userData = undefined;

    useEffect(() => {
        if(sessionStorage.getItem('questionaireData') == null) {
            userData = ["",""];
            window.sessionStorage.setItem('questionaireData', JSON.stringify(userData));
        }
        else {
            userData = JSON.parse(window.sessionStorage.getItem('questionaireData'));
            
        }
        setTextAreaData();       
      },[])



    function Submit() {
        document.getElementById("SurveyPart3").style.display = "none";
        document.getElementById("SurveyPart4").style.display = "inline";

        saveData();
        
        const data = {
            userId: userId,
            pixelData: JSON.parse(window.sessionStorage.getItem('pixelData')),
            rankData: JSON.parse(window.sessionStorage.getItem('rankData')),
            questionaireData: JSON.parse(window.sessionStorage.getItem('questionaireData')),
        }       

        fetch("https://stormy-plains-42247.herokuapp.com/submit", {
            mode: "cors",
            method: "POST",
            headers: {
                'Content-type': "application/json"
            },
            body: JSON.stringify(data)
        }).then(function(response) {
            return response.json();
        });
       
    }

    function Previous() {
        saveData();
        document.getElementById("SurveyPart2").style.display = "inline";
        document.getElementById("SurveyPart3").style.display = "none";
    }

    function saveData() {
        userData[0] = document.getElementById("areaOne").value;
        userData[1] = document.getElementById("areaTwo").value;
        window.sessionStorage.setItem('questionaireData', JSON.stringify(userData));
    }

    function setTextAreaData() {
        document.getElementById("areaOne").value = userData[0];
        document.getElementById("areaTwo").value =  userData[1];
    }

    return (
        <div id="SurveyPart3">
            <h1>Del 2</h1>
            <br>
            </br>
            <br>
            </br>
            <br>
            </br>
             <div>
            <h5 style={{textAlign: "left"}}>Frga 1</h5>
            <textarea id="areaOne"></textarea>              
            <br>
            </br>
            <br>
            </br>
            <br>
            </br>
            <br>
            </br>
            <h5 style={{textAlign: "left"}}>Frga 2</h5>
            <textarea  id="areaTwo"></textarea>      
            <br>
            </br>
            <br>
            </br>
            </div>
            <Button id="Previous-button-part3" style={{ marginBottom: "20px", marginRight: "20px", marginLeft: "20px", width: "80px"}} variant="outline-secondary" onClick={Previous}>Tillbaka</Button>
            <Button id="Next-button-part3" style={{ marginBottom: "20px", marginRight: "20px", marginLeft: "20px", width: "80px"}} variant="outline-secondary"onClick={Submit}>Frdig</Button>
        </div>
    )
}

export default SurveyPart3;