import GetModelImageData from '../utils/GetModelImageData';
import Button from 'react-bootstrap/Button';
import { useEffect } from "react";

const SurveyPart2 = () => {
    var userData = undefined;
    var modelData = GetModelImageData();

    useEffect(() => {
        if(sessionStorage.getItem('rankData') == null) {
            userData = ["","",""];
            window.sessionStorage.setItem('rankData', JSON.stringify(userData));
        }
        else {
            userData = JSON.parse(window.sessionStorage.getItem('rankData'));
        }
        setSelectData();
        document.getElementById("Next-button-part2").disabled = true;       
      },[])

    function Next() {
        saveData();
        document.getElementById("SurveyPart2").style.display = "none";
        document.getElementById("SurveyPart3").style.display = "inline";       
    }

    function Previous() {
        saveData();
        document.getElementById("SurveyPart1").style.display = "inline";
        document.getElementById("SurveyPart2").style.display = "none";
    }

    function setSelectData() {
        if(userData[0] != "") {
            document.getElementById("rank-one").value = userData[0];
        }
        if(userData[0] != "") {
            document.getElementById("rank-two").value =  userData[1];
        }
        if(userData[0] != "") {
            document.getElementById("rank-three").value =  userData[2];
        } 
    }

    function saveData() {
        userData[0] = document.getElementById("rank-one").value;
        userData[1] = document.getElementById("rank-two").value;
        userData[2] = document.getElementById("rank-three").value;
        window.sessionStorage.setItem('rankData', JSON.stringify(userData)); 
    }

    function checkIfRanked() {
        console.log("1:" +  document.getElementById("rank-one").value);
        console.log("2:" +  document.getElementById("rank-two").value);
        console.log("3:" +  document.getElementById("rank-three").value);
        
        
    }

    return (
        <div id="SurveyPart2">
            <div>
                <h1>Del 2</h1>
            <br>
            </br>
            <br>
            </br>
            </div>
            <div className="row">
                <div style={{float: "left", width: "50%"}}>
                    <div id="A" className="ModelDiv">
                    <h2 style={{display: "inline"}}>A</h2>     
                    <img src={modelData[0][0]} style={{marginLeft: "10px"}}></img>
                    <img src={modelData[1][0]} style={{marginLeft: "10px"}}></img>
                    <img src={modelData[2][0]} style={{marginLeft: "10px"}}></img>
                    <div>
                    <img src={modelData[0][1]} style={{marginRight: "10px"}}></img>
                    <img src={modelData[1][1]} style={{marginRight: "10px"}}></img>
                    <img src={modelData[2][1]} ></img>
                    </div>         
                    </div>
                    <br>
                    </br>
                    <div id="B" className="ModelDiv">
                    <h2 style={{display: "inline"}}>B</h2>    
                    <img src={modelData[3][0]} style={{marginLeft: "10px"}}></img>
                    <img src={modelData[4][0]} style={{marginLeft: "10px"}}></img>
                    <img src={modelData[5][0]} style={{marginLeft: "10px"}}></img>
                    <div>
                    <img src={modelData[3][1]} style={{marginRight: "10px"}}></img>
                    <img src={modelData[4][1]} style={{marginRight: "10px"}}></img>
                    <img src={modelData[5][1]} ></img>
                    </div>                           
                    </div>
                    <br>
                    </br>
                    <div id="C" className="ModelDiv">
                    <h2 style={{display: "inline"}}>C</h2>    
                    <img src={modelData[6][0]} style={{marginLeft: "10px"}}></img>
                    <img src={modelData[7][0]} style={{marginLeft: "10px"}}></img>
                    <img src={modelData[8][0]} style={{marginLeft: "10px"}}></img>
                    <div>
                    <img src={modelData[6][1]} style={{marginRight: "10px"}}></img>
                    <img src={modelData[7][1]} style={{marginRight: "10px"}}></img>
                    <img src={modelData[8][1]}></img>
                    </div>   
                    <br>
                    </br>
                    <br>
                    </br>            
                    </div>
                    <div>
                        <i>Hll muspekaren ver en rad fr att frstora bilderna</i>
                    </div>
                </div>
                <div style={{float: "left", width: "50%"}}>
                    <p style={{textAlign: "left"}}>Rangordna frklaringarna frn 1 till 3 baserat p den tillit de ger till att den bakomliggande AI-modellen kan identifiera djuren korrekt. Plats 1 r den frklaring som du anser ger dig mest <b>tillit</b> och plats 3 r den frklaring som ger minst.</p>
                    <div style={{textAlign: "left"}}>
                        <div style={{marginBottom: "30px", textAlign: "left"}}>
                            <h4 style={{display: "inline", marginRight: "20px"}}>1.</h4>
                            <select id="rank-one" style={{width: "150px"}} onChange={ () => { //Change to %?
                                if(document.getElementById("rank-two").value == document.getElementById("rank-one").value) {
                                    document.getElementById("rank-two").selectedIndex = 0;
                                }
                                if(document.getElementById("rank-three").value == document.getElementById("rank-one").value) {
                                    document.getElementById("rank-three").selectedIndex = 0;
                                }
                                if(
                                    document.getElementById("rank-one").value == "" || 
                                    document.getElementById("rank-two").value == "" ||
                                    document.getElementById("rank-three").value == ""
                                    ) 
                                    {
                                        console.log("Disable");
                                        document.getElementById("Next-button-part2").disabled = true;
                                    }      
                                    else 
                                    {
                                        console.log("Enable");
                                        document.getElementById("Next-button-part2").disabled = false;
                                    }                                
                            }}> 
                                <option value="" hidden>Vlj en frklaring</option>
                                <option value="A" style={{textAlign: "left"}}>A</option>
                                <option value="B" style={{textAlign: "left"}}>B</option>
                                <option value="C" style={{textAlign: "left"}}>C</option>     
                            </select>
                        </div>
                        <div style={{marginBottom: "30px", textAlign: "left"}}>
                        <h4 style={{display: "inline", marginRight: "20px"}}>2.</h4>
                            <select id="rank-two" style={{width: "150px"}} onChange={ () => { //Change to %?
                                 if(document.getElementById("rank-two").value == document.getElementById("rank-one").value) {
                                    document.getElementById("rank-one").selectedIndex = 0;
                                }
                                if(document.getElementById("rank-two").value == document.getElementById("rank-three").value) {
                                    document.getElementById("rank-three").selectedIndex = 0;
                                }
                                if(
                                    document.getElementById("rank-one").value == "" || 
                                    document.getElementById("rank-two").value == "" ||
                                    document.getElementById("rank-three").value == ""
                                    ) 
                                    {
                                        console.log("Disable");
                                        document.getElementById("Next-button-part2").disabled = true;
                                    }      
                                    else 
                                    {
                                        console.log("Enable");
                                        document.getElementById("Next-button-part2").disabled = false;
                                    }      
                        }}>
                                <option value="" hidden>Vlj en frklaring</option>
                                <option value="A" style={{textAlign: "left"}}>A</option>
                                <option value="B" style={{textAlign: "left"}}>B</option>
                                <option value="C" style={{textAlign: "left"}}>C</option> 
                            </select>
                        </div>
                        <div style={{marginBottom: "30px", textAlign: "left"}}>
                        <h4 style={{display: "inline", marginRight: "20px"}}>3.</h4>
                        <select id="rank-three" style={{width: "150px"}} onChange={ () => { //Change to %?
                                 if(document.getElementById("rank-three").value == document.getElementById("rank-one").value) {
                                    document.getElementById("rank-one").selectedIndex = 0;
                                }
                                if(document.getElementById("rank-three").value == document.getElementById("rank-two").value) {
                                    document.getElementById("rank-two").selectedIndex = 0;
                                }
                                if(
                                    document.getElementById("rank-one").value == "" || 
                                    document.getElementById("rank-two").value == "" ||
                                    document.getElementById("rank-three").value == ""
                                    ) 
                                    {
                                        console.log("Disable");
                                        document.getElementById("Next-button-part2").disabled = true;
                                    }      
                                    else 
                                    {
                                        console.log("Enable");
                                        document.getElementById("Next-button-part2").disabled = false;
                                    }     
                        }}>
                                <option value="" hidden>Vlj en frklaring</option>
                                <option value="A" style={{textAlign: "left"}}>A</option>
                                <option value="B" style={{textAlign: "left"}}>B</option>
                                <option value="C" style={{textAlign: "left"}}>C</option>  
                            </select>  
                        </div>
                    </div>
                    <div style={{textAlign: "left"}}>
                        <br>
                        </br>
                        <br>
                        </br>
                        <Button id="Previous-button-part2" style={{ marginBottom: "20px", marginRight: "20px", width: "80px"}} variant="outline-secondary" onClick={Previous}>Tillbaka</Button>
                        <Button id="Next-button-part2" style={{ marginBottom: "20px", marginLeft: "20px", width: "80px"}} variant="outline-secondary"onClick={Next}>Nsta</Button>       
                    </div>
                </div>
            </div>                         
        </div>
    )
}

export default SurveyPart2;