import colorspectrum from '../images/raw/large/Spectrum.png';
import Create3DArray from '../utils/Create3DArray';
import GetImageData from '../utils/GetImageData';
import { useNavigate } from 'react-router-dom';
import $ from 'jquery';
import { useEffect } from "react";
import Button from 'react-bootstrap/Button';

const SurveyPart1 = () => {
    let index = 1;
    let navigate = useNavigate();
    var userData = undefined;
   
    

    useEffect(() => {
        document.getElementById("animal").innerHTML =  GetImageData(index-1)[1];
        if(sessionStorage.getItem('pixelData') == null) {
            userData = Create3DArray(5,7,7);
            window.sessionStorage.setItem('pixelData', JSON.stringify(userData));
        }
        else {
            userData = JSON.parse(window.sessionStorage.getItem('pixelData'));
        }
        createPixelMask();
        SetImageValues()        
      },[])
    
    function SetImageValues() {
        var table = document.getElementById('pixel_Table')
        var row;
        for (var i = 0, row; row = table.rows[i]; i++) {
            for (var j = 0, col; col = row.cells[j]; j++) {
                if(userData[index-1][i][j] != null) {
                    $(col).css("opacity", userData[index-1][i][j]);
                }
                else {
                    $(col).css("opacity", "0.0");
                }
            }
        }
        document.getElementById("pixel_Table").style.backgroundImage = 'url(' + GetImageData(index-1)[0] + ')';
    }

    function createPixelMask(){

        var table = document.getElementById("pixel_Table");
        var row;
        var cell;
        for(var i=0;i<7;i++){
            row = table.insertRow(i);
              for(var j=0;j<7;j++){
                cell = row.insertCell(j);
                cell.addEventListener('click', IncreaseOpacity);
                cell.addEventListener('contextmenu', DecreaseOpacity)
            }
        }
    }
    
    function DecreaseOpacity(event) {
		event.preventDefault()
		var opacity_level = parseFloat($(event.target).css("opacity"));
		if (opacity_level > "0.0") {
			opacity_level = opacity_level - 0.30;
			$(event.target).css("opacity", opacity_level);
		}
	}
		
	function IncreaseOpacity(event) {
		var opacity_level = parseFloat($(event.target).css("opacity"));
		if (opacity_level < "0.9") {
			opacity_level = opacity_level + 0.30;
			$(event.target).css("opacity", opacity_level);
		}	
	}

    function NextImage() {
        saveData();
        if(index == 5) {
            Next();
        }
        else {     
        index = index + 1;
        var imageCount = index;
        document.getElementById("headline").innerHTML = "Bild " + imageCount + " av 5";
        document.getElementById("animal").innerHTML =  GetImageData(index-1)[1];
        if(index == 5) {
            document.getElementById("Next-button-part1").innerHTML = "Del 2";
        }
        SetImageValues();
        }
    }

    function PreviousImage() {
        saveData();
        if(index == 1) {
            navigate('/home')
        }
        else {         
            index = index - 1;
            var imageCount = index;
            document.getElementById("headline").innerHTML = "Bild " + imageCount + " av 5";
            document.getElementById("animal").innerHTML =  GetImageData(index-1)[1];
            if(index < 5) {
                document.getElementById("Next-button-part1").innerHTML = "Nsta";
            }
            SetImageValues();
        }
        }

    function saveData() {
        var table = document.getElementById('pixel_Table')
    		for (var i = 0, row; row = table.rows[i]; i++) {
        		for (var j = 0, col; col = row.cells[j]; j++) {
                    userData[index-1][i][j] = $(col).css("opacity");
                }
            } 
        window.sessionStorage.setItem('pixelData', JSON.stringify(userData));         
    }

    function Next() {
        document.getElementById("SurveyPart1").style.display = "none";
        document.getElementById("SurveyPart2").style.display = "inline";     
    }


    return (
        <div id="SurveyPart1">
            <h1>Del 1</h1>
            <h4 id="headline">Bild 1 av 5</h4>
                <h4 id="animal"></h4>
                    <table id="pixel_Table"></table>
                    <div>
                        <Button id="Previous-button-part1" style={{ marginBottom: "20px", marginRight: "20px", marginLeft: "20px", width: "80px"}} variant="outline-secondary" onClick={PreviousImage}>Tillbaka</Button>
                        <Button id="Next-button-part1" style={{ marginBottom: "20px", marginRight: "20px", marginLeft: "20px", width: "80px"}} variant="outline-secondary"onClick={NextImage}>Nsta</Button>       
                    </div>
                    <figure>
                    <img src={colorspectrum} alt="Spektrum" />
                        <figcaption><abbr title="Hgre niv indikerar att rutan r viktigare fr att kunna identifiera djuret."><h5>Gradskala</h5></abbr></figcaption>
                    </figure>
            </div>
    )
}

export default SurveyPart1;